<template>
  <div>
    <el-row v-if="showActionButton" style="margin-bottom: 20px;">
      <el-col :span="6">
        <span style="height: 40px;line-height: 40px">数据权限名称</span>
      </el-col>
      <el-col :span="10" :offset="1">
        <el-input placeholder="数据权限" v-model="privilegeFilter"></el-input>
      </el-col>
      <el-col :span="4" :offset="1" style="height: 40px;line-height: 40px">
        <el-button type="primary" @click="(event) => add(event)" size="medium">新增</el-button>
      </el-col>
    </el-row>
    <el-tree
        :ref="ref" :data="tree.data" node-key="id" accordion :lazy="true"
        :load="asyncLoadTreeNode"
        :filter-node-method="filterNode"
        :props="tree.defaultProps"
        :show-checkbox="false"
        :highlight-current="true"
        :expand-on-click-node="false"
        @node-click="clickNode">
    <span class="custom-tree-node" slot-scope="{ node, data }">
      <span>{{ data.name }}</span>
      <span v-if="showActionButton">
        <el-button type="text" size="mini"
                   @click="(event) => add(event, data, node)">新增</el-button>
        <el-button type="text" size="mini"
                   @click="(event) => edit(event, data, node)">修改</el-button>
        <el-button type="text" size="mini"
                   @click="(event) => remove(event, data, node)">删除</el-button>
      </span>
    </span>
  
    </el-tree>
  </div>
</template>

<script>
  export default {
    name: "privilegeTree",
    props: {
      showActionButton: {
        type: Boolean,
        require: false,
        default: false
      },
    },
    data() {
      return {
        ref: 'tree',
        privilegeFilter: '',
        tree: {
          data: [],
          defaultProps: {
            isLeaf: 'leaf'
          }
        }
      }
    },
    watch: {
      'privilegeFilter'(newV, oldV) {
        this.$refs[this.ref].filter(newV)
      }
    },
    methods: {
      async asyncLoadTreeNode(node, resolve) {
        if(node.level !== 0){
          let nodes = [];
          let result = await this.$api.privileges.gerPrivilegeList(node.data.id, '');
          result.data.data && result.data.data.forEach( node => {
            // console.log('node',node)
            nodes.push({
              id: node.id,
              code: node.code,
              name: node.name,
              parentId: node.parentId,
              path: node.path,
              leaf: node.childCount === 0
            })
          });
          resolve(nodes);
        }
      },
      async initTreeNodes() {
        // console.log('localStorage.getItem)',localStorage.getItem('dataId'))
        this.tree.data = [];
        console.log('dataId',this.dataId)
        let res = await this.$api.privileges.gerPrivilege(this.dataId);
        // console.log('res',res)
        res.data.data && this.tree.data.push({
          id: res.data.data.id,
          code: res.data.data.code,
          name: res.data.data.name,
          parentId: res.data.data.parentId,
          path: res.data.data.path,
          leaf: res.data.data.childCount === 0
        })
      },
      filterNode(value, data) {
        if (!value) {
          return true
        }
        return data.name.indexOf(value) !== -1
      },
      add(event, data, node) {
        this.$emit('add', event, data, node);
      },
      edit(event, data, node) {
        this.$emit('edit', event, data, node);
      },
      remove(event, data, node) {
        this.$emit('remove', event, data, node);
      },
      clickNode(data, node, self) {
        this.$emit('clickNode', data, node, self);
      },
      getCheckedNodes() {
        return this.$refs[this.ref].getCheckedNodes();
      },
      getElementTreeRef() {
        return this.$refs[this.ref];
      },
    },
    computed: {
      dataId() {
        return localStorage.getItem('dataId') === '' ? '-1' : localStorage.getItem('dataId');
      }
    },
    mounted() {
      this.initTreeNodes();
    },
  }
</script>

<style>
  .custom-tree-node {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    padding-right: 12px;
  }

</style>