<template>
  <div style="height: 100%;overflow: auto">
    <el-row>
      <el-form :inline="true" :model="searchForm">
        <el-col :span="24">
          <el-form-item label="角色编码">
            <el-input v-model="searchForm.content.roleCode" placeholder="请输入角色编码"></el-input>
          </el-form-item>
          <el-form-item label="角色名称">
            <el-input v-model="searchForm.content.roleName" placeholder="请输入角色名称"></el-input>
          </el-form-item>
          <el-form-item label="归属系统">
            <el-select v-model="searchForm.style.clientChecked" collapse-tags clearable multiple placeholder="请选择归属系统" ref="clientCheck">
              <el-option value="all" @click.native="checkAll">全选</el-option>
              <el-option v-for="item in searchForm.style.showClients" :key="item.clientId" :value="item.clientId" :label="item.clientName"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="refreshTable" v-loading="loading.search">查询
            </el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="add" v-shiro:hasPermission="$previlegeCodes.roles.add">新增角色
            </el-button>
          </el-form-item>
        </el-col>
        <!--<el-col :span="24">
          <el-form-item label="归属系统">
            <el-checkbox-group v-model="searchForm.style.clientChecked">
              <el-checkbox v-for="item in searchForm.style.showClients"
                           :key="item.clientId"
                           :label="item.clientId" border>{{item.clientName}}
              </el-checkbox>
            </el-checkbox-group>
          </el-form-item>
        </el-col>-->
      </el-form>
      <el-col :span="24">
        <el-table :data="tableData.data"
                  highlight-current-row
                  v-loading="loading.table"
                  style="width: 100%"
                  border>
          <el-table-column prop="roleCode" label="角色编码" show-overflow-tooltip></el-table-column>
          <el-table-column prop="roleName" label="角色名称" show-overflow-tooltip></el-table-column>
          <el-table-column prop="clientName" label="归属系统" show-overflow-tooltip min-width="160px"></el-table-column>
          <!--<el-table-column prop="roleLevelDesc" label="角色等级"></el-table-column>
          <el-table-column prop="roleTypeDesc" label="角色类型"></el-table-column>-->
          <el-table-column prop="lastModifiedAt" :formatter="lastModifiedAtFormat" min-width="120px" label="修改时间" show-overflow-tooltip>
          </el-table-column>
          <el-table-column fixed="right" min-width="200px" label="操作">
            <template slot-scope="scope">
              <el-button type="text" size="mini" v-if="scope.row.state === '10A'"
                         @click="distributeUser(scope.row)"
                         v-shiro:hasPermission="$previlegeCodes.roles.bindUser">分配用户
              </el-button>
              <el-button type="text" size="mini" v-if="scope.row.state === '10A'"
                         @click="edit(scope.row)"
                         v-shiro:hasPermission="$previlegeCodes.roles.edit">编辑
              </el-button>
              <el-button type="text" size="mini" v-if="scope.row.state === '10A'"
                         @click="deleteRole(scope.row)"
                         v-shiro:hasPermission="$previlegeCodes.roles.delete">删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination background @size-change="pageSizeChangeHandler" @current-change="pageNumberChangeHandler"
                       :page-sizes="tableData.pageList"
                       :page-size="tableData.pageSize" :current-page="tableData.pageNumber"
                       layout="sizes, prev, pager, next" :total="tableData.total">
        </el-pagination>
      </el-col>
      <el-col :span="24" style="margin-top: 10px;">

        <!--角色信息弹窗-->
        <role-dialog :openAddDialog="dialogTools.showAddDialog"
                     :openEditDialog="dialogTools.roleId"
                     @closeDialog="closeDialog"
                     @refreshTable="fetchTableData">
        </role-dialog>
        <!--分配用户-->
        <user-picker :openPicker="dialogTools.userPickerRoleId"
                     @closePicker="closePicker">
        </user-picker>
      </el-col>
    </el-row>
  </div>
</template>
<script>
  import constants from '@common/constants/constants';
  import UserPicker from '@components/roles/UserPicker';
  import RoleDialog from '@components/roles/RoleDialog';
  import {dateFormat} from '@common/util';

  export default {
    components: {
      'user-picker': UserPicker,
      'role-dialog': RoleDialog
    },
    name: 'RoleSection',
    data() {
      return {
        border: true,
        dialogTools: {
          showAddDialog: false,
          roleId: '',
          userPickerRoleId: ''
        },
        searchForm: {
          content: {
            roleCode: '',
            roleName: '',
            clients: ''
          },
          style: {
            relatedAccountDisabled: true,
            relatedUsernameDisabled: true,
            showClients: [],
            clientChecked: []
          }
        },
        tableData: {
          pageSize: constants.PAGE_SIZE,
          pageList: constants.PAGE_LIST,
          pageNumber: 1,
          data: []
        },
        loading: {
          search: false,
          table: false
        }
      };
    },
    methods: {
      checkAll(val) {
        this.searchForm.style.clientChecked = this.searchForm.style.showClients.map( (item) => item.clientId);
        this.$refs.clientCheck.blur();
      },
      /**
       * 关闭角色信息弹窗
       */
      closePicker() {
        this.dialogTools.userPickerRoleId = '';
      },
      /**
       * 关闭角色信息弹窗
       */
      closeDialog() {
        this.dialogTools.showAddDialog = false;
        this.dialogTools.roleId = '';
      },
      /**
       * 打开新增弹窗
       */
      add() {
        this.dialogTools.showAddDialog = true;
      },
      /**
       * 打开修改弹窗
       * @param{Object}row
       */
      edit(row) {
        this.dialogTools.roleId = row.roleId;
      },
      /**
       * (dialog)查询用户类型List
       */
      initPage() {
        this.$api.roles.getRelatedClients()
          .then((res) => {
            this.searchForm.style.showClients = res.data.data;
            console.log('searchForm.style.showClients', this.searchForm.style.showClients);
            let len = this.searchForm.style.showClients.length;
            for (let i = 0; i < len; i++) {
              this.searchForm.style.clientChecked.push(this.searchForm.style.showClients[i].clientId);
            }
            this.fetchTableData();
          })
          .catch((err) => {
            this.$message({
              showClose: true,
              message: err.data.meta.message,
              type: 'error'
            });
          });
      },
      /**
       * 分页条数改变监听
       * @param{Number} pageSize
       */
      pageSizeChangeHandler(pageSize) {
        this.fetchTableData(pageSize, 1);
      },
      /**
       * 分页页码改变监听
       * @param{Number} pageNumber
       */
      pageNumberChangeHandler(pageNumber) {
        this.fetchTableData(this.tableData.pageSize, pageNumber);
      },
      fetchTableData(pageSize = this.tableData.pageSize, pageNumber = this.tableData.pageNumber) {
        this.searchForm.content.clients = this.searchForm.style.clientChecked.join(',');
        this.loading.table = true;
        this.$api.roles.getRoleByPage(pageSize, pageNumber, this.searchForm.content)
          .then((res) => {
            let data = res.data.data;
            this.tableData.data = data.list;
            this.tableData.total = data.total;
            this.tableData.pageSize = data.pageSize;
            this.tableData.pageNumber = data.pageNum;
            this.loading.table = false;
            this.loading.search = false;
          })
          .catch((err) => {
            this.$message({
              showClose: true,
              message: err.data.meta.message,
              type: 'error'
            });
          });
      },
      /**
       * 刷新表格内容
       */
      refreshTable() {
        this.loading.search = true;
        this.fetchTableData();
      },
      /**
       * 分配用户
       * @param{Object} row
       */
      distributeUser(row) {
        this.dialogTools.userPickerRoleId = row.roleId;
      },
      /**
       * 删除角色
       * @param{Object} row
       */
      async deleteRole(row) {
        let queryUsersRes = await this.$api.roles.getUsersByPage(1, 1, {
          nickName: '',
          loginAcct: '',
          state: constants.state.VALID,
          pageNumber: 1,
          pageSize: 1,
          roleId: row.roleId,
          type: 'exists'
        })
        .catch((err) => {
          this.$message({
            showClose: true,
            message: err.data.meta.message,
            type: 'error'
          });
        });
        let total = queryUsersRes.data.data.total;
        if (total > 0) {
          this.$message({
            showClose: true,
            message: '该角色存在引用关系，请解除挂载后再删除！',
            type: 'error'
          });
          return false;
        }
        let tip = `即将删除【${row.roleName}】，是否确认？`;
        this.$confirm(tip, {
          type: 'warning'
        })
          .then(() => {
            this.$api.roles.deleteRole(row.roleId)
              .then((res) => {
                this.fetchTableData();
                this.$message({
                  showClose: true,
                  message: res.data.meta.message,
                  type: 'success'
                });
              })
              .catch((err) => {
                this.$message({
                  showClose: true,
                  message: err.data.meta.message,
                  type: 'error'
                });
              });
          });
      },
      lastModifiedAtFormat(row, column) {
        if (row) {
          return dateFormat(row.lastModifiedAt, 'yyyy-MM-dd hh:mm:ss');
        } else {
          return '-';
        }
      }
    },
    created() {
      this.initPage();
    }
  };
</script>
<style>
.el-table th{
   background-color:#e8f1f9;
   font-weight: 545;
   text-align: center;
}
  .el-table td{
    text-align: center;
  }
</style>

