<template>
  <el-row :gutter="50">
    <el-col :span="24">
      <el-dialog :title="dialog.title" :visible="dialog.visible" :fullscreen="true" :ref="refs.dialog"
                 @close="close" @open="afterOpen" width="90%">
        <div style="height: 700px">
          <!--left-->
          <el-col :span="12">
            <el-card shadow="never">
              <div slot="header">
                <span>未分配用户</span>
                <span class="buttonStyle">
                  <el-button type="primary" @click="leftRefreshTable" v-loading="leftLoading.search" size="medium">查询
                          </el-button>
                </span>
              </div>
              <el-header>
                <el-row>
                  <el-col :span="24">
                    <el-form label-position="left" :inline="refs.inline" :model="leftSearchForm" :ref="refs.leftForm"
                             label-width="80px">
                      <el-col :span="24">
                        <el-form-item label="账号">
                          <el-input v-model="leftSearchForm.loginAcct" placeholder="请输入账号"></el-input>
                        </el-form-item>
                        <el-form-item label="用户名">
                          <el-input v-model="leftSearchForm.nickName" placeholder="请输入用户名"></el-input>
                        </el-form-item>
                      </el-col>
                      <!-- <el-col :span="2">
                        <el-form-item>
                          <el-button type="primary" @click="leftRefreshTable" v-loading="leftLoading.search">查询
                          </el-button>
                        </el-form-item>
                      </el-col> -->
                    </el-form>
                  </el-col>
                </el-row>
              </el-header>
              <el-table :data="leftTableData.data" highlight-current-row style="width: 100%" >
                <el-table-column prop="nickName" label="用户名" show-overflow-tooltip></el-table-column>
                <el-table-column prop="loginAcct" label="账号" show-overflow-tooltip></el-table-column>
                <el-table-column label="操作">
                  <template slot-scope="scope">
                    <el-button size="small" icon="el-icon-d-arrow-right" @click="bindUser(scope.row)">
                    </el-button>
                  </template>
                </el-table-column>
              </el-table>
              <el-pagination background @size-change="leftPageSizeChangeHandler"
                             @current-change="leftPageNumberChangeHandler" :page-sizes="leftTableData.pageList"
                             :page-size="leftTableData.pageSize" :current-page="leftTableData.pageNumber"
                             layout="sizes, prev, pager, next"
                             :total="leftTableData.total">
              </el-pagination>
            </el-card>
          </el-col>
          <!--right-->
          <el-col :span="12">
            <el-card shadow="never">
              <div slot="header">
                <span>已分配用户</span>
                <span class="buttonStyle">
                  <el-button type="primary" @click="rightRefreshTable" v-loading="rightLoading.search" size="medium">查询
                  </el-button></span>
              </div>
              <el-header>
                <el-row>
                  <el-col :span="24">
                    <el-form label-position="left" label-width="80px" :inline="refs.inline" :model="rightSearchForm">
                      <el-col :span="24">
                        <el-form-item label="账号">
                          <el-input v-model="rightSearchForm.loginAcct" placeholder="请输入账号"></el-input>
                        </el-form-item>
                        <el-form-item label="用户名">
                          <el-input v-model="rightSearchForm.nickName" placeholder="请输入用户名"></el-input>
                        </el-form-item>
                      </el-col>
                      <!-- <el-col :span="2">
                        <el-form-item>
                          <el-button type="primary" @click="rightRefreshTable" v-loading="rightLoading.search">查询
                          </el-button>
                        </el-form-item>
                      </el-col> -->
                    </el-form>
                  </el-col>
                </el-row>
              </el-header>
              <el-table :data="rightTableData.data" highlight-current-row style="width: 100%">
                <el-table-column label="操作">
                  <template slot-scope="scope">
                    <el-button size="small" icon="el-icon-d-arrow-left" @click="unbindUser(scope.row)">
                    </el-button>
                  </template>
                </el-table-column>
                <el-table-column prop="nickName" label="用户名"></el-table-column>
                <el-table-column prop="loginAcct" label="账号"></el-table-column>
              </el-table>
              <el-pagination background @size-change="rightPageSizeChangeHandler"
                             @current-change="rightPageNumberChangeHandler" :page-sizes="rightTableData.pageList"
                             :page-size="rightTableData.pageSize" :current-page="rightTableData.pageNumber"
                             layout="sizes, prev, pager, next"
                             :total="rightTableData.total">
              </el-pagination>
            </el-card>
          </el-col>
        </div>
      </el-dialog>
    </el-col>
  </el-row>
</template>

<script>
  import CONSTANTS from '@common/constants/constants';
  export default {
    props: {
      'openPicker': {
        type: String,
        default: ''
      }
    },
    name: 'UserPicker',
    data() {
      return {
        border: true,
        dialog: {
          title: '分配用户',
          visible: false,
          content: {
            roleId: '',
            userStateList: []
          }
        },
        refs: {
          dialog: 'dialog',
          inline: false,
          leftForm: 'form',
          rightForm: 'form'
        },
        leftLoading: {
          search: false,
          table: false
        },
        rightLoading: {
          search: false,
          table: false
        },
        leftSearchForm: {
          nickName: '',
          loginAcct: '',
          state: CONSTANTS.state.VALID,
          pageNumber: 1,
          pageSize: 5,
          roleId: '',
          type: 'notExists'
        },
        rightSearchForm: {
          nickName: '',
          loginAcct: '',
          state: CONSTANTS.state.VALID,
          pageNumber: 1,
          pageSize: 5,
          roleId: '',
          type: 'exists'
        },
        leftTableData: {
          pageSize: 5,
          pageList: [5, 10],
          pageNumber: 1,
          data: []
        },
        rightTableData: {
          pageSize: 5,
          pageList: [5, 10],
          pageNumber: 1,
          data: []
        }
      };
    },
    methods: {
      /**
       * 分页条数改变监听
       * @param{Number} pageSize
       */
      leftPageSizeChangeHandler(pageSize) {
        this.leftFetchTableData(pageSize, 1);
      },
      rightPageSizeChangeHandler(pageSize) {
        this.rightFetchTableData(pageSize, 1);
      },
      /**
       * 分页页码改变监听
       * @param{Number} pageNumber
       */
      leftPageNumberChangeHandler(pageNumber) {
        this.leftFetchTableData(this.leftTableData.pageSize, pageNumber);
      },
      rightPageNumberChangeHandler(pageNumber) {
        this.rightFetchTableData(this.leftTableData.pageSize, pageNumber);
      },
      leftFetchTableData(pageSize = this.leftTableData.pageSize, pageNumber = this.leftTableData.pageNumber) {
        this.leftSearchForm.roleId = this.dialog.content.roleId;
        this.leftLoading.table = true;
        this.$api.roles.getUsersByPage(pageSize, pageNumber, this.leftSearchForm)
          .then((res) => {
            let data = res.data.data;
            this.leftTableData.data = data.list;
            this.leftTableData.total = data.total;
            this.leftTableData.pageSize = data.pageSize;
            this.leftTableData.pageNumber = data.pageNum;
            this.leftLoading.table = false;
            this.leftLoading.search = false;
          })
          .catch((err) => {
            this.$message({
              showClose: true,
              message: err.data.meta.message,
              type: 'error'
            });
          });
      },
      rightFetchTableData(pageSize = this.rightTableData.pageSize, pageNumber = this.rightSearchForm.pageNumber) {
        this.rightSearchForm.roleId = this.dialog.content.roleId;
        this.rightLoading.table = true;
        this.$api.roles.getUsersByPage(pageSize, pageNumber, this.rightSearchForm)
          .then((res) => {
            let data = res.data.data;
            this.rightTableData.data = data.list;
            this.rightTableData.total = data.total;
            this.rightTableData.pageSize = data.pageSize;
            this.rightTableData.pageNumber = data.pageNum;
            this.rightLoading.table = false;
            this.rightLoading.search = false;
          })
          .catch((err) => {
            this.$message({
              showClose: true,
              message: err.data.meta.message,
              type: 'error'
            });
          });
      },
      /**
       * 刷新表格内容
       */
      leftRefreshTable() {
        this.leftLoading.search = true;
        this.leftFetchTableData();
      },
      rightRefreshTable() {
        this.rightLoading.search = true;
        this.rightFetchTableData();
      },
      /**
       * 重置dialog信息
       */
      reset() {
        this.$refs[this.refs.leftForm].clearValidate();
        this.$refs[this.refs.leftForm].resetFields();

        this.$refs[this.refs.rightForm].clearValidate();
        this.$refs[this.refs.rightForm].resetFields();

        for (const key in this.leftSearchForm) {
          if (this.leftSearchForm.hasOwnProperty(key)) {
            this.leftSearchForm[key] = '';
          }
        }
        for (const key in this.rightSearchForm) {
          if (this.leftSearchForm.hasOwnProperty(key)) {
            this.rightSearchForm[key] = '';
          }
        }
        this.leftSearchForm.clientNames = [];
        this.leftSearchForm.type = 'notExists';
        this.leftSearchForm.pageSize = 5;
        this.leftSearchForm.pageNumber = 1;
        this.rightSearchForm.type = 'exists';
        this.rightSearchForm.pageSize = 5;
        this.rightSearchForm.pageNumber = 1;
        this.dialog.content.roleIdId = '';
        this.dialog.content.userStateList = [];
      },
      /**
       * 关闭弹窗1
       */
      close() {
        this.reset();
        this.dialog.visible = false;
        this.$emit('closePicker');
      },
      /**
       * 分配用户
       * @param{Object} row
       */
      bindUser(row) {
        this.$api.roles.addRoleUser(this.dialog.content.roleId, row.userId)
          .then((res) => {
            this.$message({
              showClose: true,
              message: res.data.meta.message,
              type: 'success'
            });
            Promise.all([this.rightRefreshTable(), this.leftRefreshTable()]);
          })
          .catch((err) => {
            this.$message({
              showClose: true,
              message: err.data.meta.message,
              type: 'error'
            });
          });
      },
      /**
       * 删去已分配用户
       * @param{Object} row
       */
      unbindUser(row) {
        this.$api.roles.removeRoleUser(this.dialog.content.roleId, row.userId)
          .then((res) => {
            this.$message({
              showClose: true,
              message: res.data.meta.message,
              type: 'success'
            });
            Promise.all([this.rightRefreshTable(), this.leftRefreshTable()]);
          })
          .catch((err) => {
            this.$message({
              showClose: true,
              message: err.data.meta.message,
              type: 'error'
            });
          });
      },
      /**
       * 弹窗打开后的钩子函数
       * 获取用户状态数据字典
       */
      afterOpen() {
        this.$api.users.getUserStates()
          .then((res) => {
            let data = res.data.data;
            this.dialog.content.userStateList = data;
            this.leftSearchForm.state = data[0].VALUE;
            this.rightSearchForm.state = data[0].VALUE;
            Promise.all([this.rightRefreshTable(), this.leftRefreshTable()]).catch((err) => {
              return Promise.reject(err);
            });
          })
          .catch((err) => {
            console.error(err);
            this.$message({
              showClose: true,
              message: err.data.meta.message,
              type: 'error'
            });
          });
      }
    },
    watch: {
      openPicker(roleId) {
        if (roleId !== '') {
          this.dialog.visible = true;
          this.dialog.content.roleId = roleId;
        } else {
          this.close();
        }
      }
    }
  };
</script>

<style>
.buttonStyle {
  float: right;
  margin-right: 20px;
  margin-top: -10px;
}
.el-table th{
   background-color:#e8f1f9;
   font-weight: 545;
   text-align: center;
}
  .el-table td{
    text-align: center;
  }
.el-dialog__header {
    padding: 10px 20px 10px;
    margin-left: 45px;
    margin-top: 10px;
    border: 1px solid #EBEEF5;
    width: 91%;
}
.el-dialog__headerbtn .el-dialog__close {
    color: #909399;
    margin-right: 40px;
}
.el-dialog__body {
    padding: 10px 20px;
    color: #606266;
    font-size: 14px;
    word-break: break-all;
}
</style>
