<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.visible" :fullscreen="true"
             :ref="refs.dialog" @close="close"
             width="80%" @open="afterOpen">
    <el-form :model="dialog.roleForm" :rules="dialog.rules" @submit.native.prevent :ref="refs.form"
             label-width="100px">
      <el-row>
        <el-col :span="12">
          <el-form-item label="角色编码" prop="roleCode">
            <el-input v-model="dialog.roleForm.roleCode" placeholder="请输入角色编码"></el-input>
          </el-form-item>
          <el-form-item label="角色名称" prop="roleName">
            <el-input v-model="dialog.roleForm.roleName" placeholder="请输入角色名称"></el-input>
          </el-form-item>
          <el-form-item label="数据权限" prop="dataPrivilege">
            <el-select ref="privilegeSelect" v-model="dialog.roleForm.dataPrivilege" placeholder="请输入数据权限" style="width: 100%">
              <el-option :label="dialog.roleForm.dataPrivilegeName" :value="dialog.roleForm.dataPrivilege" style="height: auto">
                <privilege-tree :show-action-button="false" @clickNode="nodeClick"></privilege-tree>
              </el-option>
            </el-select>
          </el-form-item>
          <!--<el-form-item label="角色类型" prop="roleType">
            <el-radio-group v-model="dialog.roleForm.roleType">
              <el-radio v-for="item of dialog.roleOptions.roleTypes" :key="item.KEY"
                        :label="item.KEY">{{item.VALUE}}
              </el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="角色等级" prop="roleLevel">
            <el-radio-group v-model="dialog.roleForm.roleLevel">
              <el-radio v-for="item of dialog.roleOptions.roleLevels" :key="item.KEY"
                        :label="item.KEY">{{item.VALUE}}
              </el-radio>
            </el-radio-group>
          </el-form-item>-->
          <el-form-item label="应用系统" prop="availableClients">
            <!--<el-checkbox-group v-model="dialog.roleOptions.availableClients.selected"-->
            <!--:disabled="operationDisabled"-->
            <!--@change="setRoleFormClients(this)">-->
            <!--<el-checkbox v-for="item in dialog.roleOptions.availableClients.option"-->
            <!--:key="item.value"-->
            <!--:label="item.value" border>{{item.label}}</el-checkbox>-->
            <!--</el-checkbox-group>-->
            <el-select v-model="dialog.roleOptions.availableClients.selected"
                       multiple
                       :loading="operationDisabled"
                       :disabled="operationDisabled"
                       placeholder="请选择"
                       @change="setRoleFormClients(this)">
              <el-option
                  v-for="item in dialog.roleOptions.availableClients.option"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="角色描述" prop="roleDesc">
            <el-input type="textarea"
                      :rows="2"
                      v-model="dialog.roleForm.roleDesc"
                      placeholder="角色描述"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit" :disabled="operationDisabled">提交</el-button>
            <el-button @click="reset">重置</el-button>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="资源授权" prop="resources">
            <el-tree :ref="tree.ref"
                     :data="tree.rootData"
                     :default-expanded-keys="tree.defaultExpandedKeys"
                     :highlight-current="true" node-key="id"
                     :show-checkbox="true"
                     :props="tree.defaultProps">
            </el-tree>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
  </el-dialog>
</template>

<script>
  import ResourceTree from '@components/resources/ResourceTree';
  import PrivilegeTree from '../privileges/PrivilegeTree'
  export default {
    name: 'RoleDialog',
    props: {
      'openAddDialog': {
        type: Boolean,
        default: false
      },
      'openEditDialog': {
        type: String,
        default: ''
      }
    },
    components: {
      'resource-tree': ResourceTree,
      PrivilegeTree
    },
    computed: {
      trimmedRoleFormData: (vm) => {
        return Object.assign({}, vm.dialog.roleForm, {
          roleCode: vm.dialog.roleForm.roleCode.trim(),
          roleName: vm.dialog.roleForm.roleName.trim()
        });
      }
    },
    data() {
      return {
        roleId: '',
        operationDisabled: false,
        tree: {
          ref: 'tree',
          defaultExpandedKeys: ['rootId'],
          defaultCheckedKeys: [],
          rootData: [],
          clientData: [],
          defaultProps: {
            isLeaf: 'leaf'
          }
        },
        refs: {
          dialog: 'dialog',
          form: 'form'
        },
        dialog: {
          title: '',
          visible: false,
          roleForm: {
            roleId: '',
            roleCode: '',
            roleName: '',
            roleType: '',
            roleLevel: '',
            availableClients: [],
            resources: [],
            roleDesc: '',
            dataPrivilege: '',
            dataPrivilegeName: ''
          },
          roleOptions: {
            roleLevels: [],
            roleTypes: [],
            availableClients: {
              selected: [],
              option: []
            }
          },
          rules: {
            roleCode: [{
              required: true,
              trigger: 'blur',
              validator: (rule, value, callback) => {
                if (!this.dialog.roleForm.roleCode) {
                  callback(new Error('请输入角色编码'));
                } else
                  if(this.dialog.roleForm.roleCode
                      && /[\u4e00-\u9fa5]+/.test(this.dialog.roleForm.roleCode)){
                  callback(new Error('请输入非中文字符'));
                } else {
                    callback();
                }
              }
            }],
            roleName: [{
              required: true,
              message: '角色名称不能为空',
              trigger: 'blur'
            }],
            dataPrivilege: [{
              required: true,
              message: '数据权限不能为空',
              trigger: 'blur'
            }],
            /*roleType: [{
              required: true,
              message: '角色类型不能为空',
              trigger: 'change'
            }],
            roleLevel: [{
              required: true,
              message: '角色等级不能为空',
              trigger: 'change'
            }],*/
            availableClients: [{
              validator: (rule, value, callback) => {
                if (this.dialog.roleOptions.availableClients.selected.length === 0) {
                  callback(new Error('请选择应用系统'));
                } else {
                  callback();
                }
              },
              trigger: 'change',
              required: true
            }]
          }
        }
      };
    },
    methods: {
      /**
       * 获取client信息，这里把nodeKey改成"C_xxx"是因为clientId与resourceId有重复情况
       * @param{String} clientId
       * @return{Promise} Promise
       */
      async getClientNodeData(clientId) {
        let response = await this.$api.roles.getSyncResourceForTree({
          clientId: clientId,
          roleId: this.roleId
        });
        let client = response.data.data;
        // 有此节点权限则为叶子节点则添加勾选
        this.tree.defaultCheckedKeys = [];
        // 需要默认勾选的节点
        if (client.checkedResources !== undefined) {
          this.tree.defaultCheckedKeys = client.checkedResources;
        }
        // 对于半选的节点，我们对其默认展开，方面查看其子节点的勾选情况
        if (client.halfCheckedResources !== undefined) {
          this.tree.defaultExpandedKeys = client.halfCheckedResources;
        }
        // 对于没有子节点的系统，我们对其进行默认勾选（其实并不起什么作用，看起来比较舒服）
        if (client.resourceCount === 0) {
          this.tree.defaultCheckedKeys.push(client.id);
        }
        // 默认展开系统节点
        this.tree.defaultExpandedKeys.push(client.id);
        return client;
      },

      /**
       * 勾选（移除）系统checkbox时生成（移除）相应树节点
       */
      setRoleFormClients() {
        // 生成（移除）树节点前禁止掉系统勾选操作和提交按钮操作
        this.operationDisabled = true;
        // form表单中的（Array）clientId
        let availableClients = this.dialog.roleForm.availableClients;
        let len = availableClients.length;
        // checkbox被选中的（Array）clientId
        let length = this.dialog.roleOptions.availableClients.selected.length;
        if (length > len) {
          // 给用户添加新的系统权限
          for (let i = 0; i < length; i++) {
            if (availableClients.indexOf(this.dialog.roleOptions.availableClients.selected[i])
                === -1) {
              let clientId = this.dialog.roleOptions.availableClients.selected[i];
              // 创建相应系统的树节点
              this.getClientNodeData(clientId).then((clientNodeData) => {
                this.$refs[this.tree.ref].append(clientNodeData, 'rootId');
                this.$refs[this.tree.ref].setCheckedKeys(this.tree.defaultCheckedKeys);
                this.dialog.roleForm.availableClients = this.dialog.roleOptions.availableClients.selected;
                this.operationDisabled = false;
              }).catch((err) => {
                console.error(err);
              });
            }
          }
        } else {
          // 移除用户系统权限
          for (let j = 0; j < len; j++) {
            if (this.dialog.roleOptions.availableClients.selected.indexOf(availableClients[j])
                === -1) {
              let nodeKey = `C_${availableClients[j]}`;
              // 移除相应系统的树节点
              this.$refs[this.tree.ref].remove(nodeKey);
              // 重新给form表单中系统有权限的clientId
              this.dialog.roleForm.availableClients = this.dialog.roleOptions.availableClients.selected;
              this.operationDisabled = false;
            }
          }
        }
        this.operationDisabled = false;
      },
      /**
       * dialog打开时：获取角色类型，角色等级，系统
       */
      afterOpen() {
        // 设置树根节点
        this.tree.rootData.push({
          id: 'rootId',
          label: '根',
          leaf: false,
          disabled: true,
          nodeType: 'rootType',
          children: []
        });
        Promise.all([
          this.$api.roles.getRoleTypes(), // 获取角色类型
          this.$api.roles.getRoleLevels() // 获取角色等级
        ]).then(([roleTypes, roleLevels]) => {
          this.dialog.roleOptions.roleTypes = roleTypes.data.data;
          this.dialog.roleOptions.roleLevels = roleLevels.data.data;
          // 编辑时
          this.roleId !== '' ? this.getRoleByRoleId(this.roleId) : this.getAvailableClients();
        }).catch((err) => {
          this.$message({
            showClose: true,
            message: err.data.meta.message,
            type: 'error'
          });
        });
      },
      /**
       * 获取系统信息（checkbox）
       */
      getAvailableClients() {
        this.$api.roles.getAvailableClients(this.roleId).then((availableClients) => {
          this.dialog.roleOptions.availableClients.option = availableClients.data.data;
          let options = this.dialog.roleOptions.availableClients.option;
          let len = options.length;
          for (let i = 0; i < len; i++) {
            // 系统权限默认选中
            if (options[i].selected === 1) {
              this.dialog.roleOptions.availableClients.selected.push(options[i].value);
              this.dialog.roleForm.availableClients.push(options[i].value);
              this.operationDisabled = true;
              // 已有系统权限默认生成树节点
              this.getClientNodeData(options[i].value)
              .then((clientNodeData) => {
                this.$refs[this.tree.ref].append(clientNodeData, 'rootId');
                this.$refs[this.tree.ref].setCheckedKeys(this.tree.defaultCheckedKeys);
                this.operationDisabled = false;
              });
            }
          }
        })
        .catch((err) => {
          this.$message({
            showClose: true,
            message: err.data.meta.message,
            type: 'error'
          });
        });
      },
      getAvailableClientsWithDataPrivilege (dataPrivilegeId) {
        return this.$api.roles.getAvailableClients(this.roleId, dataPrivilegeId).then((availableClients) => {
          this.dialog.roleOptions.availableClients.option = availableClients.data.data
          this.operationDisabled = false
        }).catch((err) => {
          this.operationDisabled = false
          this.$message({
            showClose: true,
            message: err.data.meta.message,
            type: 'error'
          })
        })
      },
      /**
       * 根据roleId获取角色信息
       * @param{String} roleId
       */
      getRoleByRoleId(roleId) {
        this.$api.roles.getRoleByRoleId(this.roleId)
        .then((res) => {
          this.dialog.roleForm = res.data.data;
          this.dialog.roleForm.roleLevel = this.dialog.roleForm.roleLevel + '';
          this.dialog.roleForm.availableClients = [];
          this.dialog.roleForm.resources = [];
          this.getAvailableClients();
        })
        .catch((err) => {
          this.$message({
            showClose: true,
            message: err.data.meta.message,
            type: 'error'
          });
        });
      },
      /**
       * 弹出新增页面
       */
      showAddRoleDialog() {
        this.dialog.title = '新增';
        this.dialog.visible = true;
      },
      /**
       * 弹出新增页面
       * @param{String} roleId
       */
      showEditRoleDialog() {
        this.dialog.title = '修改';
        this.dialog.visible = true;
      },
      onSubmit() {
        this.$refs[this.refs.form].validate((valid) => {
          if (valid) {
            let halfCheckedNodes = this.$refs[this.tree.ref].getHalfCheckedNodes();
            let checkedNodes = this.$refs[this.tree.ref].getCheckedNodes();
            let nodes = halfCheckedNodes.concat(checkedNodes);
            nodes.forEach((node) => {
              if (node.nodeType === 'R') {
                this.dialog.roleForm.resources.push(node.id);
              }
            });
            let promise = this.roleId === '' ?
                this.$api.roles.addRole(this.trimmedRoleFormData) :
                this.$api.roles.editRole(this.trimmedRoleFormData);

            promise.then((response) => {
              if (response.data.meta.success === false) {
                this.$message({
                  dangerouslyUseHTMLString: true,
                  showClose: true,
                  message: response.data.meta.message,
                  type: 'error'
                });
              } else {
                this.$message({
                  showClose: true,
                  message: response.data.meta.message,
                  type: 'success'
                });
                this.dialog.visible = false;
                this.$emit('refreshTable');
              }
            }).catch((error) => {
              console.error(error);
              this.$message({
                dangerouslyUseHTMLString: true,
                showClose: true,
                message: error.meta.message,
                type: 'error'
              });
            });
          }
        });
      },
      reset() {
        this.clear();
        this.afterOpen();
      },
      clear() {
        this.$refs[this.refs.form].clearValidate();
        this.$refs[this.refs.form].resetFields();
        for (const key in this.dialog.roleForm) {
          if (this.dialog.roleForm.hasOwnProperty(key)) {
            this.dialog.roleForm[key] = '';
          }
        }
        this.dialog.roleForm.availableClients = [];
        this.dialog.roleOptions.availableClients.selected = [];
        this.dialog.roleForm.resources = [];
        this.dialog.roleForm.dataPrivilege = '';
        this.dialog.roleForm.dataPrivilegeName = '';
        this.$refs[this.tree.ref].remove('rootId');
        this.tree.defaultExpandedKeys = ['rootId'];
        this.tree.defaultCheckedKeys = [];
        this.tree.rootData = [];
      },
      close() {
        this.roleId = '';
        this.clear();
        this.$emit('closeDialog');
      },
      nodeClick (data) {
        this.dialog.roleForm.dataPrivilege = data.id
        this.dialog.roleForm.dataPrivilegeName = data.name
        this.operationDisabled = true
        this.dialog.roleOptions.availableClients.option = []
        this.dialog.roleOptions.availableClients.selected = []
        this.getAvailableClientsWithDataPrivilege(data.id).then(() => {
          this.setRoleFormClients()
        })
        this.$refs.privilegeSelect.blur()
      }
    },
    watch: {
      openAddDialog(boolean) {
        if (boolean) {
          this.showAddRoleDialog();
        } else {
          this.close();
        }
      },
      openEditDialog(roleId) {
        if (roleId !== '') {
          this.roleId = roleId;
          this.showEditRoleDialog();
        } else {
          this.close();
        }
      }
    }
  };
</script>
